/* New button animations */
.btn-pulse {
    position: relative;
    overflow: hidden;
  }
.btn-pulse::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.1);
    border-radius: inherit;
    transform: scale(0);
    opacity: 0;
    transition: transform 0.5s, opacity 0.5s;
  }
.btn-pulse:hover::after {
    opacity: 1;
    transform: scale(1);
  }
.btn-ripple {
    position: relative;
    overflow: hidden;
  }
.btn-ripple::after {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
    background-image: radial-gradient(circle, #fff 10%, transparent 10.01%);
    background-repeat: no-repeat;
    background-position: 50%;
    transform: scale(10, 10);
    opacity: 0;
    transition: transform 0.5s, opacity 1s;
  }
.btn-ripple:active::after {
    transform: scale(0, 0);
    opacity: 0.2;
    transition: 0s;
  }
